body {
  margin:0;
  padding:0;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color:#EFF3F7;
  height:100%;
}

html, #root{
  height:100%;
}

.App {
  padding:50px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size:14px;
}

.App--loading{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}